import { BROWSER_NAME } from './config';

export const webPushPermissionInfo = permission => {
  switch (permission) {
    case 'unsupported':
      return `Acest browser (${BROWSER_NAME}) nu permite notificari`;
    case 'denied':
      return `Notificarile sunt dezactivate din ${BROWSER_NAME}`;
    case 'default':
      return `Apasati pentru activare notificari`;
    case 'granted':
      return 'Notificari active';
    default:
      throw new Error(`Unknown web push permission ${permission}`);
  }
};

export default null;
