import AutoSizer from 'react-virtualized-auto-sizer';
import { VariableSizeList } from 'react-window';
import * as React from 'react';
import { useResetReactWindowCache } from '../hooks';
import Time from '../time';
import Day from '../day';
import Event from '../event';

const DAY_HEIGHT = 45;
const DAY_EVENT_HEIGHT = 45;

const DayEvent = ({ event }) => {
  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'row',
        height: DAY_EVENT_HEIGHT,
        alignItems: 'center',
      }}
    >
      <Time
        hideDate
        style={{ marginLeft: 8, width: 40 }}
        timestamp={event.timeSync.toMillis()}
      />
      <Event style={{ marginLeft: 16, width: 180 }} event={{ ...event }} />
    </div>
  );
};
const Row = ({ data: days, index, style }) => {
  const day = days[index];
  return (
    <div style={style}>
      <Day
        style={{
          position: 'sticky',
          top: 0,
          background: '#889aff',
          height: DAY_HEIGHT,
          zIndex: 1,
        }}
        timestamp={day.timestamp}
      />
      {day.events.map(event => (
        <DayEvent event={event} key={event.id} />
      ))}
    </div>
  );
};

const getDayLayoutHeight = day =>
  DAY_HEIGHT + day.events.length * DAY_EVENT_HEIGHT;

const addBanknotes = events => {
  let lastBanknotes = null;
  return events
    .reverse()
    .map(event => {
      switch (event.type) {
        case 'reset-credit-total':
          lastBanknotes = {
            '1': 0,
            '5': 0,
            '10': 0,
            '50': 0,
          };
          return event;
        case 'add-cash':
          if (lastBanknotes === null) {
            lastBanknotes = {
              '1': 0,
              '5': 0,
              '10': 0,
              '50': 0,
            };
          }
          lastBanknotes = {
            ...lastBanknotes,
            [event.amount]: lastBanknotes[event.amount] + 1,
          };
          return {
            ...event,
            banknotes: lastBanknotes,
          };
        default:
          return event;
      }
    })
    .reverse();
};

export default ({ events }) => {
  const days = addBanknotes(events).reduceRight((days, event) => {
    const midnight = new Date(event.timeSync.toMillis());
    midnight.setHours(0, 0, 0, 0);
    const timestamp = midnight.getTime();
    const [lastDay = null, ...otherDays] = days;

    if (lastDay === null) {
      return [{ timestamp, events: [event] }, ...otherDays];
    }
    if (lastDay.timestamp !== timestamp) {
      return [{ timestamp, events: [event] }, lastDay, ...otherDays];
    }
    return [{ timestamp, events: [event, ...lastDay.events] }, ...otherDays];
  }, []);
  const estimatedHeight =
    days.reduce((sum, day) => sum + getDayLayoutHeight(day), 0) / days.length;

  const reactWindowListRef = useResetReactWindowCache(events);

  return (
    <div style={{ flex: 1 }}>
      <AutoSizer>
        {({ height, width }) => (
          <VariableSizeList
            ref={reactWindowListRef}
            width={width}
            height={height}
            itemCount={days.length}
            estimatedItemSize={estimatedHeight}
            itemSize={index => getDayLayoutHeight(days[index])}
            itemKey={index => days[index].timestamp}
            itemData={days}
          >
            {Row}
          </VariableSizeList>
        )}
      </AutoSizer>
    </div>
  );
};
