import React from 'react';
import GoogleButton from 'react-google-button';
import firebase from 'firebase'
import { AppBar, Toolbar, Button, Typography, Avatar, MenuItem, Menu, IconButton, } from '@material-ui/core';
import { BrowserRouter, HashRouter, Route, Switch, Link, } from 'react-router-dom';
import { Home, SupervisorAccount } from '@material-ui/icons';
import Login from './login';
import DevicesContainer from './devices-container';
// import Device from './device/index.jsx';
import Device from './NewDevice';

import MissingPermission from './missing-permission';

import Users from './users';
import axios from 'axios';
// import WebPushStatus from './web-push-status';
const Router = BrowserRouter

class DeviceList extends React.Component {
  componentDidMount = () => {
  }
  render() {
    return (
      <Router>
        <div style={{ display: 'flex', flexDirection: 'column' }}>
          <AppBar position="static">
            <Toolbar>
              <IconButton component={Link} to="/" edge="start" color="inherit">
                <Home />
              </IconButton>

              {true ? (
                <IconButton
                  component={Link}
                  to="/users"
                  edge="start"
                  color="inherit"
                >
                  <SupervisorAccount />
                </IconButton>
              ) : null}

              <Typography variant="h6" style={{ flex: 1 }}>
                {"Awesome App"}
              </Typography>
              <Button
                color="inherit"
                onClick={({ currentTarget }) => {
                  //   setMenuTarget(currentTarget);
                }}
              >
                {/* {userData.displayName} */}
                <Avatar
                  //   src={userData.photoURL}
                  style={{
                    margin: 10,
                    width: 24,
                    height: 24,
                  }}
                />
              </Button>
            </Toolbar>
            {/* <WebPushStatus email={'princehamzi.mine@gmail.com'} /> */}
          </AppBar>


          <Menu
            // anchorEl={menuTarget}
            keepMounted
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'right',
            }}
            transformOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            getContentAnchorEl={null}
            // open={menuTarget !== null}
            open={false}
          // onClose={() => setMenuTarget(null)}
          >
            <MenuItem
              onClick={() => {
                // setMenuTarget(null);
                // firebaseApp.logout();
              }}
            >
              Log out
          </MenuItem>
          </Menu>



          {false ? (
            <MissingPermission />
          ) : (
            <Switch>
              <Route path="/" exact component={DevicesContainer} />
              <Route path="/users" component={Users} />
              <Route
                path="/device/:id"
                component={({ match }) => <Device id={match.params.id} />}
              />
              <Route component={() => '404'} />
            </Switch>
          )}



        </div>
      </Router>
    )
  }

}

export default DeviceList