import * as React from 'react';
import { Switch, FormControlLabel, Button } from '@material-ui/core';
import { useFirestore } from 'react-redux-firebase';
import { firebase } from '@firebase/app';
import { Warning as WarnIcon } from '@material-ui/icons';
import { useAuthEmail, useUserData, useNotificationPermission } from '../hooks';
import { webPushPermissionInfo } from '../i18n';

export default ({ device }) => {
  const userEmail = useAuthEmail();
  const userData = useUserData(userEmail);

  const isWatched =
    userData === null
      ? null
      : (userData.watchedDevices || []).includes(device.id);

  const { notificationPermission, triggerPrompt } = useNotificationPermission();

  const firestoreDB = useFirestore();

  const setIsWatched = newIsWatched => {
    if (newIsWatched && notificationPermission === 'default') {
      triggerPrompt();
    }
    const { FieldValue } = firebase.firestore;
    firestoreDB
      .collection('emails')
      .doc(userEmail)
      .update({
        watchedDevices: (newIsWatched
          ? FieldValue.arrayUnion
          : FieldValue.arrayRemove)(device.id),
      });
  };

  const sw = (
    <Switch
      checked={isWatched === true}
      onChange={(_, newValue) => {
        setIsWatched(newValue);
      }}
    />
  );

  const cantWatch = isWatched === true && notificationPermission !== 'granted';

  return (
    <div>
      <FormControlLabel
        style={{ margin: 18 }}
        disabled={isWatched === null || cantWatch}
        control={sw}
        label={`Notificari ${isWatched ? 'active' : 'inactive'}`}
      />
      {cantWatch ? (
        <Button
          color="primary"
          variant="contained"
          onClick={triggerPrompt}
          disabled={notificationPermission === 'denied'}
          startIcon={<WarnIcon />}
        >
          {webPushPermissionInfo(notificationPermission)}
        </Button>
      ) : null}
    </div>
  );
};
