import AutoSizer from 'react-virtualized-auto-sizer';
import { VariableSizeList } from 'react-window';
import * as React from 'react';
import { Typography } from '@material-ui/core';
import { useResetReactWindowCache } from '../hooks';
import { MONTH_ABSOLUTE } from '../translation';
import DayTime from '../day-time';
// import { TRANSACTION_DEBOUNCE_INTERVAL } from '../config';

const Month = ({ timestamp, amount, style }) => {
  return (
    <div
      style={{
        ...style,
        paddingLeft: 16,
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
      }}
    >
      <Typography variant="subtitle2" noWrap>
        {`${MONTH_ABSOLUTE(timestamp)} - ${amount} RON`}
      </Typography>
    </div>
  );
};

const DAY_HEIGHT = 45;
const DAY_EVENT_HEIGHT = 45;

const DayEvent = ({ timestamp, amount }) => {
  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'row',
        height: DAY_EVENT_HEIGHT,
        alignItems: 'center',
      }}
    >
      <DayTime style={{ marginLeft: 16, width: 170 }} timestamp={timestamp} />
      <Typography variant="body2" noWrap align="right" style={{ width: 50 }}>
        {amount}
      </Typography>
      <Typography variant="body2" noWrap>
        &nbsp;RON
      </Typography>
    </div>
  );
};
const Row = ({ data: months, index, style }) => {
  const month = months[index];
  return (
    <div style={style}>
      <Month
        style={{
          position: 'sticky',
          top: 0,
          background: '#889aff',
          height: DAY_HEIGHT,
        }}
        timestamp={month.timestamp}
        amount={month.amount}
      />
      {month.events.map(({ timestamp, amount }) => (
        <DayEvent timestamp={timestamp} amount={amount} key={timestamp} />
      ))}
    </div>
  );
};

const getDayLayoutHeight = day =>
  DAY_HEIGHT + day.events.length * DAY_EVENT_HEIGHT;

const groupByMonths = events =>
  events.reduceRight((months, event) => {
    const startOfMonth = new Date(event.timeSync.toMillis());
    startOfMonth.setDate(1);
    startOfMonth.setHours(0, 0, 0, 0);
    const timestamp = startOfMonth.getTime();
    const [lastDay = null, ...otherDays] = months;

    if (lastDay === null) {
      return [
        { timestamp, amount: event.amount, events: [event] },
        ...otherDays,
      ];
    }
    if (lastDay.timestamp !== timestamp) {
      return [
        { timestamp, amount: event.amount, events: [event] },
        lastDay,
        ...otherDays,
      ];
    }
    return [
      {
        timestamp,
        amount: lastDay.amount + event.amount,
        events: [event, ...lastDay.events],
      },
      ...otherDays,
    ];
  }, []);

const sumByTransaction = events =>
  events.reduceRight((months, event) => {
    const timestamp = event.timeSync.toMillis();
    const [lastTransaction = null, ...otherDays] = months;

    if (lastTransaction === null) {
      return [{ timestamp, amount: event.amount }, ...otherDays];
    }
    // if (timestamp - lastTransaction.timestamp > TRANSACTION_DEBOUNCE_INTERVAL) {
    //   return [
    //     { timestamp, amount: event.amount },
    //     lastTransaction,
    //     ...otherDays,
    //   ];
    // }
    return [
      {
        timestamp: lastTransaction.timestamp,
        amount: event.amount + lastTransaction.amount,
      },
      ...otherDays,
    ];
  }, []);

export default ({ events }) => {
  const cashEvents = events.filter(
    ({ type }) => type === 'add-cash' || type === 'add-card'
  );

  const months = groupByMonths(cashEvents).map(
    ({ timestamp, amount, events }) => ({
      timestamp,
      amount,
      events: sumByTransaction(events),
    })
  );
  const estimatedHeight =
    months.reduce((sum, day) => sum + getDayLayoutHeight(day), 0) /
    months.length;

  const reactWindowListRef = useResetReactWindowCache(cashEvents);

  return (
    <div style={{ flex: 1 }}>
      <AutoSizer>
        {({ height, width }) => (
          <VariableSizeList
            ref={reactWindowListRef}
            width={width}
            height={height}
            itemCount={months.length}
            estimatedItemSize={estimatedHeight}
            itemSize={index => getDayLayoutHeight(months[index])}
            itemKey={index => months[index].timestamp}
            itemData={months}
          >
            {Row}
          </VariableSizeList>
        )}
      </AutoSizer>
    </div>
  );
};
