import AutoSizer from 'react-virtualized-auto-sizer';
import { VariableSizeList } from 'react-window';
import * as React from 'react';
import { Typography } from '@material-ui/core';
import { useResetReactWindowCache } from '../hooks';
import { MONTH_ABSOLUTE } from '../translation';
import DayWeek from '../day-week';

const Month = ({ timestamp, amountCash, amountCard, style }) => {
  return (
    <div
      style={{
        ...style,
        paddingLeft: 16,
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
      }}
    >
      <Typography variant="subtitle2" noWrap>
        {`${MONTH_ABSOLUTE(
          timestamp
        )} - ${amountCash} RON (cash) ${amountCard} RON (card)`}
      </Typography>
    </div>
  );
};

const DAY_HEIGHT = 45;
const DAY_EVENT_HEIGHT = 45;

const DayEvent = ({ timestamp, amountCash, amountCard }) => {
  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'row',
        height: DAY_EVENT_HEIGHT,
        alignItems: 'center',
      }}
    >
      <DayWeek style={{ width: 60 }} timestamp={timestamp} />
      <Typography variant="body2" noWrap align="right" style={{ width: 50 }}>
        {amountCash}
      </Typography>
      <Typography variant="body2" noWrap>
        &nbsp;RON (cash)
      </Typography>
      <Typography variant="body2" noWrap align="right" style={{ width: 50 }}>
        {amountCard}
      </Typography>
      <Typography variant="body2" noWrap>
        &nbsp;RON (card)
      </Typography>
    </div>
  );
};

const Row = ({ data: months, index, style }) => {
  const month = months[index];
  return (
    <div style={style}>
      <Month
        style={{
          position: 'sticky',
          top: 0,
          background: '#889aff',
          height: DAY_HEIGHT,
        }}
        timestamp={month.timestamp}
        amountCash={month.amountCash}
        amountCard={month.amountCard}
      />
      {month.events.map(({ timestamp, amountCash, amountCard }) => (
        <DayEvent
          timestamp={timestamp}
          amountCash={amountCash}
          amountCard={amountCard}
          key={timestamp}
        />
      ))}
    </div>
  );
};

const getDayLayoutHeight = day =>
  DAY_HEIGHT + day.events.length * DAY_EVENT_HEIGHT;

const groupByMonths = events =>
  events.reduceRight((months, event) => {
    const startOfMonth = new Date(event.timeSync.toMillis());
    startOfMonth.setDate(1);
    startOfMonth.setHours(0, 0, 0, 0);
    const timestamp = startOfMonth.getTime();
    const [lastDay = null, ...otherDays] = months;

    if (event.type === 'add-cash') {
      if (lastDay === null) {
        return [
          {
            timestamp,
            amountCash: event.amount,
            amountCard: 0,
            events: [event],
          },
          ...otherDays,
        ];
      }
      if (lastDay.timestamp !== timestamp) {
        return [
          {
            timestamp,
            amountCash: event.amount,
            amountCard: 0,
            events: [event],
          },
          lastDay,
          ...otherDays,
        ];
      }
      return [
        {
          timestamp,
          amountCash: lastDay.amountCash + event.amount,
          amountCard: lastDay.amountCard,
          events: [event, ...lastDay.events],
        },
        ...otherDays,
      ];
    }
    if (event.type === 'add-card') {
      if (lastDay === null) {
        return [
          {
            timestamp,
            amountCash: 0,
            amountCard: event.amount,
            events: [event],
          },
          ...otherDays,
        ];
      }
      if (lastDay.timestamp !== timestamp) {
        return [
          {
            timestamp,
            amountCash: 0,
            amountCard: event.amount,
            events: [event],
          },
          lastDay,
          ...otherDays,
        ];
      }
      return [
        {
          timestamp,
          amountCash: lastDay.amountCash,
          amountCard: lastDay.amountCard + event.amount,
          events: [event, ...lastDay.events],
        },
        ...otherDays,
      ];
    }
    throw new Error(`Unknown event type ${event.type}`);
  }, []);

const sumByDay = events =>
  events.reduceRight((months, event) => {
    const startOfDay = new Date(event.timeSync.toMillis());
    startOfDay.setHours(0, 0, 0, 0);
    const timestamp = startOfDay.getTime();
    const [lastDay = null, ...otherDays] = months;

    if (event.type === 'add-cash') {
      if (lastDay === null) {
        return [
          { timestamp, amountCash: event.amount, amountCard: 0 },
          ...otherDays,
        ];
      }
      if (lastDay.timestamp !== timestamp) {
        return [
          { timestamp, amountCash: event.amount, amountCard: 0 },
          lastDay,
          ...otherDays,
        ];
      }
      return [
        {
          timestamp,
          amountCash: event.amount + lastDay.amountCash,
          amountCard: lastDay.amountCard,
        },
        ...otherDays,
      ];
    }
    if (event.type === 'add-card') {
      if (lastDay === null) {
        return [
          { timestamp, amountCash: 0, amountCard: event.amount },
          ...otherDays,
        ];
      }
      if (lastDay.timestamp !== timestamp) {
        return [
          { timestamp, amountCash: 0, amountCard: event.amount },
          lastDay,
          ...otherDays,
        ];
      }
      return [
        {
          timestamp,
          amountCash: lastDay.amountCash,
          amountCard: event.amount + lastDay.amount,
        },
        ...otherDays,
      ];
    }
    throw new Error(`Unknown event type ${event.type}`);
  }, []);

export default ({ events }) => {
  const cashEvents = events.filter(
    ({ type }) => type === 'add-cash' || type === 'add-card'
  );

  const months = groupByMonths(cashEvents).map(
    ({ timestamp, amountCash, amountCard, events }) => ({
      timestamp,
      amountCash,
      amountCard,
      events: sumByDay(events),
    })
  );
  const estimatedHeight =
    months.reduce((sum, day) => sum + getDayLayoutHeight(day), 0) /
    months.length;

  const reactWindowListRef = useResetReactWindowCache(cashEvents);

  return (
    <div style={{ flex: 1 }}>
      <AutoSizer>
        {({ height, width }) => (
          <VariableSizeList
            ref={reactWindowListRef}
            width={width}
            height={height}
            itemCount={months.length}
            estimatedItemSize={estimatedHeight}
            itemSize={index => getDayLayoutHeight(months[index])}
            itemKey={index => months[index].timestamp}
            itemData={months}
          >
            {Row}
          </VariableSizeList>
        )}
      </AutoSizer>
    </div>
  );
};
