
import React from 'react'
import { TextField, Tab, Tabs, Button, Tooltip } from '@material-ui/core';
import SignalBarIcon from './signal-bar-icon';
import { Link, useRouteMatch } from 'react-router-dom';
import { addCredit } from './user-api';
import DeviceEvents from './device/events';
import CashMonthly from './device/cash-monthly';
import CashTransaction from './device/cash-transaction';
import Notifications from './device/notifications';
import GsmSignal from './device/gsm-signal';
import Checkbox from '@material-ui/core/Checkbox';
import Event from './event';
import axios from 'axios'

import { Table, TableHead, TableBody, TableRow, TableCell, Typography } from '@material-ui/core';
import DayTime from './day-time';

const sortedDevices = [
  { Device: "id1", name: "hi" }
]

// const eventsWithResetTotal = [];

// const deviceWithID = { ...device, id: deviceID };
var ActiveTab, activePath

export default class Device extends React.Component {

  constructor() {
    super()
    this.state = {
      sortedEvents: [],
      DeviceName: '',
      Loading: true,
      showCashReport: false
    }
  }

  componentDidMount = () => {
    activePath = screens[0].path
    ActiveTab = screens.find(({ path }) => path === activePath).component;


    // Loading:true

    let deviceId = (window.location.href.split('/')[4])

    let Url = window.location.href.includes('local') ? 'http://localhost:5000' : 'https://nameless-coast-52007.herokuapp.com'

    axios.get(`${Url}/api/getdeviceinfo/${deviceId}`).then((res) => {
      this.setState({ Loading: true })
      this.setState({ DeviceName: res.data.info.name })
    })

    axios.get(`${Url}/api/getdeviceevents/${deviceId}`).then((res) => {
      let arr = []
      res.data.data.forEach((item) => {
        arr.push(item)
      })

      //alert(arr.length)
      this.setState({ Loading: false })
      this.setState({ sortedEvents: arr })
    })
    // activePath =  startupPath !== null ? startupPath : screens[0].path





  }



  onClick = () => {
    // const confirmed = window.confirm(
    //   `Sigur doriti sa adaugati ${addCreditValue} credit pentru ${device.name}?`
    // );
    // if (confirmed) {
    //   addCredit(deviceID, Number(addCreditValue));
    // }
  }

  onSigur = () => {
    // const confirmed = window.confirm(
    //   `Sigur doriti sa resetati credit-ul pentru ${device.name}?`
    // );
    // if (confirmed) {
    //   const now = firestore.Timestamp.now();
    //   deviceRef
    //     .collection('events')
    //     .doc(generateID())
    //     .set({
    //       type: 'reset-credit-total',
    //       timeSync: firestore.FieldValue.serverTimestamp(),
    //       timeLocal: now,
    //     });
    //   deviceRef.update({
    //     moneyReset: now,
    //   });
    // }
  }

  handleRoute = () => {
    let param = (window.location.href.split('/')[5])
    if (param == 'cash-report') {
      alert("Raport")
      this.setState({ showCashReport: true })
    }
  }

  render() {
    return (
      <div style={Styles.Flex1}>
        <div
          style={Styles.Flex2}
        >
          <div
            style={Styles.Flex3}
          >

            <Tooltip
              title={
                "title"
                // typeof device.rssiDB === 'number'
                //   ? `${device.rssiDB} db`
                //   : 'Semnal GSM necunoscut'
              }
            >
              <SignalBarIcon
                rssiDB={"device.rssiDB"}
                style={{ paddingTop: 24, marginRight: 12 }}
              />
            </Tooltip>

            <TextField
              label={'Denumire'}
              value={this.state.DeviceName}
              // onChange={({ target: { value } }) => {
              //   deviceRef.update({ name: value });
              // }}
              margin="normal"
            />

            <div style={{ width: 24 }} />

            <TextField
              label="Adaugare credit"
              value={this.state.DeviceName}
              // defaultValue={'addCreditValue.toString()'}
              // onChange={({ target: { value } }) => {
              //   setAddCreditValue(Number(value));
              // }}
              margin="normal"
            />

            <Button onClick={() => { this.onClick() }}  >
              Adaugare credit
            </Button>

          </div>

          <div style={Styles.Flex4}  >
            <Button onClick={() => { addCredit('deviceID', 0) }}  >    Resetare acceptor    </Button>

            <Button onClick={() => { this.onSigur() }}>    Resetare credit  </Button>

            <div
              style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
              }}
            >
              <Typography align="center" variant="subtitle2">
                Ultimul reset la credit
            </Typography>
              {/* <DayTime
                timestamp={'deviceMoneyResetTimestamp'}
                style={{ alignItems: 'center' }}
              /> */}
            </div>

          </div>
        </div>



        <Tabs
          variant="scrollable"
          value={activePath}
          scrollButtons="on"
          onChange={(_, newPath) => {
            // setActivePath(newPath);
          }}
        >
          {screens.map(({ path, title }) => (
            <Tab
              key={Math.random()}
              onClick={() => { this.handleRoute() }}
              value={path}
              component={Link}
              label={title}
              to={`/device/cMeucuqYV2b/${path}`}
            />
          ))}
        </Tabs>

        {/* <ActiveTab
          events={this.state.sortedEvents.map(
            ({ timeSync, timeLocal, ...event }) => {
              return { ...event, timeLocal, timeSync: timeSync || timeLocal };
            }
          )}
        /> */}




        {/* slider */}

        {/* blueLine */}

        <div style={Styles.Blue} />

        {/* simple React Map */}

        {!this.state.showCashReport &&

          <div style={Styles.EventListContainer} >
            {this.state.sortedEvents.map(item => {
              return (
                <div key={Math.random()} style={Styles.EventItems} >

                  <div style={Styles.cell} >
                    {item.timeSyncOnly}
                  </div>

                  <div style={Styles.cell} >
                    <Event event={item} />
                  </div>

                </div>
              )
            })}
          </div>


        }






      </div>

    )
  }
}


const Styles = ({
  Flex1: {
    display: 'flex',
    flexDirection: 'column',
    flex: 1
  },
  Flex2: {
    display: 'flex',
    justifyContent: 'space-between',
    flexWrap: 'wrap',
  },
  Flex3: {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    margin: '0 16px',
  },
  Flex4: {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    margin: '0 16px',
  },
  Blue: {
    position: 'absolute',
    top: '27%',
    left: "0%",
    backgroundColor: "skyblue",
    width: "100%",
    height: "5%"
  },
  EventListContainer: {
    position: 'absolute',
    top: '35%',
    left: "0%",
    width: "100%",
    height: "4500%",
    overflow: "auto",
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'flex-start',
    alignItems: 'flex-start'
  },
  EventItems: {
    height: '30%',
    width: '100%',
    marginBottom: '2%',
    overflow: "hidden",
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-start',
    alignItems: 'flex-start'
  },
  cell: {
    height: '100%',
    width: '20%',
    marginRight: '5%',
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-start',
    alignItems: 'center'
  }

})


const screens = [
  {
    component: DeviceEvents,
    title: 'Evenimente',
    path: 'events',
  },
  {
    component: CashMonthly,
    title: 'Raport Bani Lunar',
    path: 'cash-report',
  },
  {
    component: CashTransaction,
    title: 'Raport Bani Tranzactii',
    path: 'cash-transaction',
  },
  {
    component: Notifications,
    title: 'Notificari',
    path: 'notifications',
  },
  {
    component: GsmSignal,
    title: 'Semnal GSM',
    path: 'gsm-signal',
  },
].map(({ component, title, path }) => ({
  component,
  title,
  path,
  fullPath: `localhost:3000/device/ekNhZncrnKm/${path}`,
}));

